<template>
    <div>
        <el-card>
            <el-descriptions class="margin-top" title="课程信息" :column="2" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <user />
                            </el-icon>
                            课程名称
                        </div>
                    </template>
                    {{ course.name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            任课老师
                        </div>
                    </template>
                    {{ course.teacher_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            院系
                        </div>
                    </template>
                    {{ course.department }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            年级
                        </div>
                    </template>
                    {{ course.grade }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            创建时间
                        </div>
                    </template>
                    {{ course.create_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            默认角色
                        </div>
                    </template>
                    {{ course.default_role_name }}
                </el-descriptions-item>
            </el-descriptions>
            <el-descriptions direction="vertical" :column="1" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            简介
                        </div>
                    </template>
                    <pre>{{ course.introduction }}</pre>
                </el-descriptions-item>
            </el-descriptions>
        </el-card>
        <el-card class="header">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm">
                <el-form-item><el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-button type="primary" @click="getDatas">查询</el-button>
            </el-form>
        </el-card>
        <el-card>
            <el-table :data="tableData" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="name" label="学生名称" min-width="200">
                </el-table-column>
                <el-table-column prop="role_name" label="角色" min-width="200">
                </el-table-column>
                <el-table-column prop="class_name" label="班级名称" min-width="200">
                </el-table-column>
                <el-table-column prop="class_department" label="院系" min-width="200">
                </el-table-column>
            </el-table>
            <div v-if="tableData.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>
    </div>
</template>
<script>
import { course_details, course_show_student, course_exit } from "@/api/teacher/api.js";
// eslint-disable-next-line no-unused-vars
export default {
    data() {
        return {

            course: {
                name: "",
                teacher_name: "",
                department: "",
                grade: "",
                create_time: "",
            },
            tableData: [],

            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            //查询
            searchHeaderForm: {
                find: '',
            },

        }
    },
    created() {
        this.getUserMessage()
        this.getDatas()
    },
    methods: {
        async getUserMessage() {
            var course_id = this.$route.query.course_id
            course_details({ _id: course_id }).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.course = res.data.data

                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        async getDatas() {
            const params = {
                course_id: this.$route.query.course_id,
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
            }
            const res = await course_show_student(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    this.total = res.data.data.count
                    if (res.data.data.data == null) {
                        this.tableData = []
                    } else {
                        this.tableData = res.data.data.data
                    }
                } else {
                    this.total = res.data.data.count
                    this.tableData = []
                }
            }
        },

    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-top: 20px;
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}
</style>
  